/*========= ページネーションCSS ===============*/
.pagination {
  position: fixed;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1em;
  z-index: 10;
  list-style: none;
}

@media screen and (max-width: 768px) {
  .pagination {
    right: 1.4vw;
  }
}

.pagination a {
  display: block;
  height: 20px;
  margin-bottom: 5px;
  color: #000;
  position: relative;
  padding: 4px;
}

@media screen and (max-width: 768px) {
  .pagination a {
    margin-bottom: 0;
  }
}

.pagination a.active:after {
  box-shadow: inset 0 0 0 5px;
}

.pagination a .hover-text {
  display: none;
}

.pagination a:hover .hover-text {
  opacity: 1;
}

.pagination a:after {
  -webkit-transition: box-shadow 0.5s ease;
  transition: box-shadow 0.5s ease;
  width: 10px;
  height: 10px;
  display: block;
  border: 1px solid;
  border-radius: 50%;
  content: "";
  position: absolute;
  margin: auto;
  top: 0;
  right: 3px;
  bottom: 0;
}

@media screen and (max-width: 768px) {
  .pagination a:after {
    width: 1.6vw;
    height: 1.6vw;
  }
}

/*現在地表示のテキストの設定*/
/*768px以下は現在地表示のテキストを非表示*/
@media screen and (max-width: 768px) {
  .pagination a .hover-text {
    display: none;
  }
}

